import React, { Component } from 'react';
import { connect } from 'react-redux';

class About extends Component {
    render() {
    
        return (
            <div className="rl_container">
                <div className="format_about">
                    <h2>  </h2>
                    <h4>   : <a href="https://vk.com/genomusApp">https://vk.com/genomusApp</a></h4>
                    <h4> : example@gmail.com</h4>
                    <h4>     apk.  Android  </h4>
                    <p>  QR-code: </p>
                    <img 
                        alt='robots' 
                        src={`https://api.qrserver.com/v1/create-qr-code/?size=250x250&data=${this.props.user.login.genId}`} 
                        className="qr"
                    />
                </div>
            </div>
        );
    }
}

function mapStateProps(state) {
    return {
        user:state.user
    }
}

export default connect(mapStateProps)(About)
